import { _decorator, Component, Sprite, SpriteFrame, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ImageSwitcher')
export class ImageSwitcher extends Component {
    @property([SpriteFrame])
    images: SpriteFrame[] = [];

    @property(Sprite)
    displaySprite: Sprite = null;

    private currentIndex: number = 0;

    start() {
        this.updateImage();
    }

    switchImage() {
        this.currentIndex = (this.currentIndex + 1) % this.images.length;
        this.updateImage();
    }

    updateImage() {
        if (this.images.length > 0 && this.displaySprite) {
            this.displaySprite.spriteFrame = this.images[this.currentIndex];
        }
    }
}
